<template>
  <div class="gatheringtimer">
    <textarea v-model="Macro" cols="50" rows="30" readonly></textarea>
  </div>
</template>

<script>
const POINTS = [
  {job: '', level: 802, time: 0, name: '', place: ''},
  {job: '', level: 800, time: 0, name: '', place: ''},
  {job: '', level: 76, time: 0, name: '', place: ''},
  {job: '', level: 802, time: 2, name: '', place: ''},
  {job: '', level: 801, time: 2, name: '', place: ''},
  {job: '', level: 802, time: 4, name: '', place: ''},
  {job: '', level: 801, time: 4, name: '', place: ''},
  {job: '', level: 76, time: 4, name: '', place: ''},
  {job: '', level: 802, time: 6, name: '', place: ''},
  {job: '', level: 800, time: 6, name: '2', place: ''},
  {},
  {job: '', level: 802, time: 8, name: '', place: ''},
  {job: '', level: 801, time: 8, name: '', place: ''},
  {job: '', level: 76, time: 8, name: '', place: ''},
  {job: '', level: 802, time: 10, name: '', place: ''},
  {job: '', level: 801, time: 10, name: '', place: ''},
  {job: '', level: 802, time: 12, name: '', place: ''},
  {job: '', level: 800, time: 12, name: '', place: ''},
  {job: '', level: 80, time: 12, name: '', place: ''},
  {job: '', level: 802, time: 14, name: '', place: ''},
  {job: '', level: 801, time: 14, name: '3', place: ''},
  {},
  {job: '', level: 802, time: 16, name: '', place: ''},
  {job: '', level: 801, time: 16, name: '', place: ''},
  {job: '', level: 80, time: 16, name: '', place: ''},
  {job: '', level: 802, time: 18, name: '', place: ''},
  {job: '', level: 800, time: 18, name: '', place: ''},
  {job: '', level: 802, time: 20, name: '', place: ''},
  {job: '', level: 801, time: 20, name: '', place: ''},
  {job: '', level: 76, time: 20, name: '', place: ''},
  {job: '', level: 802, time: 22, name: '', place: ''},
  {job: '', level: 801, time: 22, name: '1', place: ''}
];

export default {
  components: {},
  data() {
    return {
      points: POINTS
    };
  },
  methods: {},
  computed: {
    Macro() {
      var t;
      return this.points.map(i => {
        if(!i.job) return '';
        t = i.time < 10 ? '0' + i.time : i.time;
        if(i.level === 802) t += '00';
        else if(i.level > 80) t += '20';
        else t += '40';
        return `/alarm ${i.job}${i.place}${i.name} et ${t} 0`;
      }).join('\n');
    }
  }
}
</script>

<style lang="scss">
.gatheringtimer {
  padding: 1em 2em;
  textarea {
    padding: .5em 1em;
    width: 100%;
    height: 50em;
    line-height: 1.5;
    font-size: 12px;
    resize: none;
  }
}
</style>


